import { CostForm, CostMessagge, HeaderPage, Tabs } from "Components/Appraisal";

const Costs = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
		hasValuationType,
	},
}) => {
	console.log(hasValuationType);
	const tabs = [
		{
			title : "Costos",
			Tab   : props => hasValuationType ? <CostForm {...props} /> : <CostMessagge {...props} />,
		},
	];

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[2, 1]}
				section="footer"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>

			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
		</>
	);
};

export default Costs;
