import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import { useHandlerMutation }      from "Helpers/Hooks";
import Costs                       from "./Costs";

export const PARAMS =  {
	select_fields : [ "cost", "conclusions.type_of_valuation"],
};

const DEFAULT_FETCH_DATA = {
	cost        : "",
	conclusions : { type_of_valuation : null },
};
const CostsContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {
	const { id : appraiseId } = useParams();

	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const defaultValues = { cost : appraise.cost };

	const { type_of_valuation } = appraise.conclusions;

	const hookForm = useForm({
		defaultValues,
	});

	const { handleSubmit } = hookForm;

	const onSubmit =  handleSubmit(async (formData) => {
		await editApprasial({
			module : `/appraisals/cost/${appraiseId}`,
			method : "PUT",
			data   : formData,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<Costs
			delegations={{
				onSubmit,
				hookForm,
				hasValuationType : !!type_of_valuation?.value,
				isLoading        : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(CostsContainer, PARAMS);
